import * as React from 'react';
import headerToBack from '../../../assets/image/header/header_to_back.svg';

interface NavigationBarComponentProps {
  callback: () => void;
  pageTitle: string;
}

interface NavigationBarComponentState {
}

export class NavigationBarComponent extends React.Component<NavigationBarComponentProps, NavigationBarComponentState> {

  constructor(props: NavigationBarComponentProps) {
    super(props);
  }

  render() {
    const pageTitle = this.props.pageTitle;
    return (
      <div>
        <button
          className={'reset inHeaderBtn inHeaderLeftBtn'}
          onClick={() => this.props.callback()}
        >
          <img src={headerToBack} />
        </button>

        <div className={'headerTitle'}>
          {pageTitle}
        </div>
      </div>
    );
  }

}


