import * as React from 'react';
import {ColorGenerator} from '../../../../util/color-generator';
import {Item} from '../item';

interface ItemListComponentProps {
  callbackSelectItem: (item: Item) => void,
  itemList: Item[],
}

interface ItemListComponentState {
  itemList: Item[];
}

export class ItemListComponent extends React.Component<ItemListComponentProps, ItemListComponentState> {

  constructor(props: ItemListComponentProps) {
    super(props);
    this.state = {
      itemList: this.props.itemList,
    };
  }

  componentWillReceiveProps(nextProps: Readonly<ItemListComponentProps>, nextContext: any): void {
    this.setState({
      itemList: nextProps.itemList,
    });
  }

  render() {
    const callbackSelectItem = this.props.callbackSelectItem;
    const itemList = this.state.itemList;
    return (

      <div className={'suggestInner_wrap'}>
        {itemList.map((item: Item) => (
          <div
            key={item.id}
            onClick={() => callbackSelectItem(item)}
          >
            {item.label}
          </div>
        ))}
      </div>

    );
  }

}



