import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import {DebugService} from '../../../../service/debug-service';
import {ConfigComponent} from '../../../common/config-component/config-conponent';
import './page-home-component.scss';
import homeDotted from '../../../../assets/image/home/home_dotted.svg';

interface PageWebHomeComponentProps {
}

interface PageWebHomeComponentState {
  isDisplay: boolean;
}

export class PageHomeComponent extends React.Component<PageWebHomeComponentProps, PageWebHomeComponentState> {

  private menuList: menuList[] = [
    {label: 'みんなの駅とは', path: RoutingPass.ABOUT},
    {label: 'みんなの投稿一覧', path: RoutingPass.REFINE_ALL_CONTRIBUTION},
    {label: '投稿する', path: RoutingPass.POST_CONTRIBUTION},
    {label: '過去投稿一覧', path: RoutingPass.MY_CONTRIBUTION},
    {label: 'おしらせ', path: RoutingPass.INFO},
  ];

  constructor(props: PageWebHomeComponentProps) {
    super(props);
    this.state = ({
      isDisplay: true,
    });
  }

  render() {
    const isDisplay = this.state.isDisplay;
    return (
      <div>

        <ConfigComponent callbackDisplayHome={(flag) => this.callbackDisplayConfig(flag)}/>

        {isDisplay ? (
          <div className={'body_area home_body'}>
            <div className={'bodyInner_wrap'}>
              {this.menuList.map((menu) => (
                <div
                  className={'btn_wrap'}
                  key={menu.label}
                >
                  <button
                    className={'reset position_r'}
                    onClick={() => this.gotoPage(menu.path)}
                  >
                    {menu.label}
                    <span className={'innerBorder'}>
                    <img src={homeDotted} />
                  </span>
                  </button>
                </div>
              ))}
            </div>
          </div>
        ) : (<></>)}

      </div>
    );
  }

  private gotoPage(path: string): void {
    this.props['history'].push(path);
  }

  private callbackDisplayConfig(isDisplay: boolean): void {
    this.setState({
      isDisplay: isDisplay,
    });
  }

}

interface menuList {
  label: string;
  path: string;
}



