import * as React from 'react';
import './config-pass-component.scss'

interface ConfigPassComponentProps {
}

interface ConfigPassComponentState {
  inputPassCurrent: string,
  inputPassNew: string,
  inputPassNewConfirm: string,
}

export class ConfigPassComponent extends React.Component<ConfigPassComponentProps, ConfigPassComponentState> {

  constructor(props: ConfigPassComponentProps) {
    super(props);
    this.state = {
      inputPassCurrent: '',
      inputPassNew: '',
      inputPassNewConfirm: '',
    };
  }

  render() {
    return (
      <div className={'body_area pass_body'}>

        <div className={'listStyle_wrap passChangeList'}>
          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              現在のパスワード
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPassCurrent: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              新しいパスワード
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPassNew: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left position_r'}>
              新しいパスワード<div className={'abs_text'}>(確認)</div>
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPassNewConfirm: e.target.value})}
              />
            </div>
          </div>
        </div>

        <div className={'divide30'} />

        <div className={'btn_wrap'}>
          <button
            className={'reset btnStyle_1'}
            onClick={() => this.handlerClickButtonOk()}
          >
            変更する
          </button>
        </div>

        <div className={'divide20'} />

        <div className={'bottomText'}>
          パスワードをお忘れの時は、<span onClick={() => {
        //
        }}>パスワード再設定手続き</span>をご利用ください。
        </div>

      </div>
    );
  }

  private handlerClickButtonOk(): void {
    console.log('hoge----');
  }

}



