import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import './page-info-component.scss';
import iconArrowRight from '../../../../assets/image/common/icon_arrow_right.svg';
import {InfoDetailComponent} from './info-detail-component/info-detail-component';

interface PageWebInfoComponentProps {
}

interface PageWebInfoComponentState {
  infoList: InfoData[];
  infoData: InfoData | null;
}

export class PageInfoComponent extends React.Component<PageWebInfoComponentProps, PageWebInfoComponentState> {

  constructor(props: PageWebInfoComponentProps) {
    super(props);
    this.state = {
      infoList: [],
      infoData: null,
    };
  }

  componentWillMount(): void {
    this.setState({
      infoList: [
        new InfoData('2019年1月28日', '新大阪駅での設備変更に関して',
          '当社では.........'),
        new InfoData('2018年12月11日', '年末年始の運行に関して',
          '当社では.........'),
      ],
    });
  }

  render() {
    const infoList = this.state.infoList;
    const infoData = this.state.infoData;
    return (
      <div>

        <div className={'header_wrap info_head'}>
          <NavigationBarComponent
            callback={() => infoData ? this.setState({infoData: null}) : this.props['history'].push(RoutingPass.HOME)}
            pageTitle={'お知らせ'}
          />
        </div>

        <div
          className={infoData ? 'body_area info_body infoDetail_body' : 'body_area info_body'}
        >
          <div className={'bodyInner_wrap'}>

            {infoData ? (
              <>
                <InfoDetailComponent
                  infoData={infoData}
                />
              </>
            ) : (
              <div className={'listStyle_wrap infoList'}>
                {infoList.map((data) => (
                  <button
                    key={data.title}
                    onClick={() => this.handlerClickGotoDetail(data)}
                    className={'listItem_wrap reset flex_box flex_align_center'}
                  >
                    <div className={'listItem_left'}>
                      <div className={'infoItemDate'}>{data.date}</div>
                      <div className={'infoItemTitle'}>{data.title}</div>
                    </div>
                    <div className={'listItem_right'}>
                      <img src={iconArrowRight}/>
                    </div>
                  </button>
                ))}
              </div>
            )}

          </div>
        </div>
      </div>
    );
  }

  private handlerClickGotoDetail(infoData: InfoData): void {
    this.setState({
      infoData: infoData,
    });
  }

}

export class InfoData {
  constructor(
    public date: string,
    public title: string,
    public text: string,
  ) {
  }


}


