import * as React from 'react';
import {Redirect, Route, Switch} from 'react-router';
import {PageAdminLoginComponent} from './component/page/admin/login/page-admin-login-component';
import {PageLoginComponent} from './component/page/web/login/page-login-component';
import {RoutingPass} from './constant/routing-pass';
import {PageHomeComponent} from './component/page/web/home/page-home-component';
import {PageAboutComponent} from './component/page/web/about/page-about-component';
import {PageInfoComponent} from './component/page/web/info/page-info-component';
import {PageContributionPostComponent} from './component/page/web/contribution-post/page-contribution-post-component';
import {PageContributionRefineComponent} from './component/page/web/contribution-refine/page-contribution-refine-component';
import {PageContributionListComponent} from './component/page/web/contribution-list/page-contribution-list-component';
import {PageContributionDetailComponent} from './component/page/web/contribution-detail/page-contribution-detail-component';
import {ForgetComponent} from './component/page/web/forget-component/forget-component';

class App extends React.Component {
  public render() {
    return (
      <div className={'src_web'}>
        {/* TODO 上のclassNameはadminとwebで切り替える */}

        {/* routing */}
        <Switch>

          {/** admin **/}
          <Route path={RoutingPass.ADMIN} component={PageAdminLoginComponent}/>

          {/** web **/}
          {/** login */}
          <Route path={RoutingPass.LOGIN} component={PageLoginComponent}/>
          {/** home **/}
          <Route path={RoutingPass.HOME} component={PageHomeComponent}/>
          {/** about **/}
          <Route path={RoutingPass.ABOUT} component={PageAboutComponent}/>
          {/** contribution-detail **/}
          <Route path={RoutingPass.CONTRIBUTION_DETAIL} render={(props) => <PageContributionDetailComponent history={props.history} isAll={false} />}/>
          {/** contribution **/}
          <Route path={RoutingPass.POST_CONTRIBUTION} render={(props) => <PageContributionPostComponent history={props.history} isFromList={false}/>}/>
          {/** contribution from list **/}
          <Route path={RoutingPass.POST_CONTRIBUTION_FROM_LIST} render={(props) => <PageContributionPostComponent history={props.history} isFromList={true} /> }/>
          {/** my-contribution **/}
          <Route path={RoutingPass.MY_CONTRIBUTION} render={(props) => <PageContributionListComponent history={props.history} isAll={false} />}/>
          {/** all-contribution-detail **/}
          <Route path={RoutingPass.ALL_CONTRIBUTION_DETAIL} render={(props) => <PageContributionDetailComponent history={props.history} isAll={true} />}/>
          {/** all-contribution-list **/}
          <Route path={RoutingPass.ALL_CONTRIBUTION_LIST} render={(props) => <PageContributionListComponent history={props.history} isAll={true} />}/>
          {/** refine-all-contribution **/}
          <Route path={RoutingPass.REFINE_ALL_CONTRIBUTION} component={PageContributionRefineComponent}/>
          {/** info **/}
          <Route path={RoutingPass.INFO} component={PageInfoComponent}/>
          {/** retake pss **/}
          <Route path={RoutingPass.FORGET} component={ForgetComponent}/>

          {/** redirect **/}
          <Redirect to={RoutingPass.LOGIN}/>

        </Switch>

        {/* debug */}
        {/*<DebugComponent />*/}

      </div>
    );
  }
}

export default App;



