import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import {AutoCompleteInputComponent} from '../../../common/auto-complete-input-component/auto-complete-input-component';
import {QueryParamFormatter} from '../../../../util/query-param-formatter';
import {Item} from '../../../common/auto-complete-input-component/item';
import './page-contribution-refine-component.scss';
import iconArrowDown from '../../../../assets/image/common/icon_arrow_down.svg';
import {CorrespondStatus} from '../../../../enum/correspond-status';
import {CorrespondStatusCheckServie} from '../../../../service/correspond-status-check-servie';
import {StationList} from '../../../../dummy-data/stationList';
import {Route} from '../../../../model/route';
import {RouteList} from '../../../../dummy-data/routeList';

interface PageWebRefineContributionComponentProps {
}

interface PageWebRefineContributionComponentState {
  route: string;
  station: string;
  status: CorrespondStatus;
}

export class PageContributionRefineComponent extends React.Component<PageWebRefineContributionComponentProps, PageWebRefineContributionComponentState> {

  private statusList: {status: CorrespondStatus, label: string}[] = [
    {status: CorrespondStatus.NotYet, label: CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(CorrespondStatus.NotYet)},
    {status: CorrespondStatus.During, label: CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(CorrespondStatus.During)},
    {status: CorrespondStatus.Complete, label: CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(CorrespondStatus.Complete)},
    {status: CorrespondStatus.Accepted, label: CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(CorrespondStatus.Accepted)},
  ];

  private route: RouteList = new RouteList();
  private station: StationList = new StationList();

  constructor(props: PageWebRefineContributionComponentProps) {
    super(props);
    this.state = {
      route: '',
      station: '',
      status: CorrespondStatus.NotYet,
    };
  }

  render() {
    const route = this.state.route;
    const station = this.state.station;
    // const status = this.state.status;
    return (
      <div>

        <div className={'header_wrap postListAll'}>
          <NavigationBarComponent
            callback={() => this.props['history'].push(RoutingPass.HOME)}
            pageTitle={'みんなの投稿一覧'}
          />
        </div>

        <div className={'body_area refinePostList'}>
          <div className={'bodyInner_wrap'}>

            <div className={'refineHead'}>
              路線と駅を指定して投稿を見ることができます
            </div>

            <div className={'divide20'} />

            <div className={'refineListItem flex_box flex_align_center'}>
              <div className={'innerLeft'}>
                路線
              </div>
              {/*<input*/}
              {/*onChange={(e) => this.setState({route: e.target.value})}*/}
              {/*value={route}*/}
              {/*/>*/}
              <div className={'innerRight position_r flex_box flex_align_center'}>
                <div>
                  <AutoCompleteInputComponent
                    getList={(searchQuery) => this.getRouteListData(searchQuery) }
                    onChange={(inputValue) => {
                      this.setState({route: inputValue.label});
                    }}
                  />
                </div>
                <div>
                  <img src={iconArrowDown} />
                </div>
              </div>
            </div>

            <div className={'refineListItem flex_box flex_align_center'}>
              <div className={'innerLeft'}>
                駅
              </div>
              <div className={'innerRight position_r flex_box flex_align_center'}>
                <div>
                  {/*<input*/}
                    {/*onChange={(e) => this.setState({station: e.target.value})}*/}
                    {/*value={station}*/}
                  {/*/>*/}
                  <div>
                    <AutoCompleteInputComponent
                      getList={(searchQuery) => this.getStationListData(searchQuery) }
                      onChange={(inputValue) => {
                        this.setState({station: inputValue.label});
                      }}
                    />
                  </div>
                </div>
                <div>
                  <img src={iconArrowDown} />
                </div>
              </div>
            </div>

            <div className={'refineListItem flex_box flex_align_center'}>
              <div className={'innerLeft'}>
                ステータス
              </div>
              <div className={'innerRight flex_box flex_align_center'}>
                <div>
                  <select
                    onChange={(e) => this.setState({status: +e.target.value})}
                  >
                    {this.statusList.map((status) => (
                      <option
                        key={status.status}
                        value={status.status}
                      >
                        {status.label}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <img src={iconArrowDown} />
                </div>
              </div>
            </div>

            <div className={'divide30'} />

            <div className={'btn_wrap'}>
              <button
                className={'reset btnStyle_1'}
                onClick={() => this.handlerClick()}
              >
                投稿一覧へ
              </button>
            </div>

          </div>
        </div>

      </div>
    );
  }

  private handlerClick(): void {
    const route = this.state.route;
    const station = this.state.station;
    const status = CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(this.state.status);
    const queryStr: string = QueryParamFormatter.queryCompose({route: route, station: station, status: status});
    this.props['history'].push(RoutingPass.ALL_CONTRIBUTION_LIST + '?' + queryStr);
  }

  private getRouteListData(inputValue: string): Item[] {
    const list: Item[] = [];
    const arr = this.route.getList(inputValue);
    arr.map((data, i) => {
      list.push(new Item(i, data.name));
    });
    return list;
  }

  private getStationListData(inputValue: string): Item[] {
    const list: Item[] = [];
    const arr = this.station.getList(inputValue);
    arr.map((data, i) => {
      list.push(new Item(i, data.name));
    });
    return list;
  }

}



