import * as React from 'react';
import './info-detail-component.scss';
import {InfoData} from "../page-info-component";

interface InfoDetailComponentProps {
  infoData: InfoData;
}

interface InfoDetailComponentState {
}

export class InfoDetailComponent extends React.Component<InfoDetailComponentProps, InfoDetailComponentState> {

  constructor(props: InfoDetailComponentProps) {
    super(props);
  }

  render() {
    const infoData = this.props.infoData;
    return (
      <div className={'infoDetailInner'}>

        <div className={'infoDetailTitle'}>
          {infoData.title}
        </div>

        <div className={'infoDetailDate'}>
          {infoData.date}
        </div>

        <div className={'infoDetailText'}>
          {infoData.text}
        </div>

      </div>
    );
  }

}


