import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import './page-login-component.scss';
import loginLogo from '../../../../assets/image/login/login_logo.svg';
import loginWhite from '../../../../assets/image/login/white.png';
import loginline from '../../../../assets/image/login/line.svg';
import {CreateAccountComponent} from './create-account-component/create-account-component';
import {ForgetComponent} from '../forget-component/forget-component';
import {SendMailComponent} from './send-mail-component/send-mail-component';

interface PageWebLoginComponentProps {
}

interface PageWebLoginComponentState {
  email: string;
  password: string;
  isDisplayCreateAccount: boolean;
  isDisplayRetakePass: boolean;
}

export class PageLoginComponent extends React.Component<PageWebLoginComponentProps, PageWebLoginComponentState> {

  constructor(props: PageWebLoginComponentProps) {
    super(props);
    this.state = {
      email: '',
      password: '',
      isDisplayCreateAccount: false,
      isDisplayRetakePass: false,
    };
  }

  render() {
    const email = this.state.email;
    const password = this.state.password;
    const isDisplayCreateAccount = this.state.isDisplayCreateAccount;
    const isDisplayRetakePass = this.state.isDisplayRetakePass;
    return (
      <>
        {isDisplayCreateAccount ? (
          <CreateAccountComponent callbackIsDisplay={() => this.setState({isDisplayCreateAccount: false})}/>
        ) : (
          <>
            {isDisplayRetakePass ? (
              <SendMailComponent callbackIsDisplay={() => this.setState({isDisplayRetakePass: false})}/>
            ) : (
              <div className={'login_wrap'}>

                <div className={'login_head'}> {/* ロゴ */}
                  <img className={'login_head_logo'} src={loginLogo} alt="みんなの駅"/>
                  <img className={'login_head_white'} src={loginWhite} alt="みんなの駅"/>
                </div>

                <div className={'body_area login_body'}> {/* body */}

                  <div>
                    <input
                      className={'textInput'}
                      onChange={(e) => this.setState({email: e.target.value})}
                      value={email}
                      placeholder={'メールアドレス'}
                    />

                    <div className={'divide20'}/>

                    <input
                      className={'textInput'}
                      type={'password'}
                      onChange={(e) => this.setState({password: e.target.value})}
                      value={password}
                      placeholder={'パスワード'}
                    />
                  </div>

                  <div className={'divide20'}/>

                  <button className={'reset btnStyle_1'} onClick={() => this.checkLogin()}>ログイン</button>

                  <div className={'divide20'}/>

                  <div className={'pass_forget'}>パスワードをお忘れの時は、<span onClick={() => this.handlerClickRetakePass()}>パスワード再設定手続き</span>をご利用ください。
                  </div>

                  <div className={'divide30'}/>

                  <div className={'line_wrap'}>
                    <img src={loginline}/>
                  </div>

                  <div className={'divide30'}/>

                  <div className={'font_s_12'}>ご利用がはじめての方は新規ユーザー登録をしてください。</div>

                  <div className={'divide20'}/>

                  <div>
                    <button className={'reset btnStyle_1'} onClick={() => this.handlerClickCreateAccount()}>新規ユーザー登録
                    </button>
                  </div>

                </div>

              </div>
            )}
          </>
        )}
      </>
    );
  }

  private checkLogin(): void {
    this.props['history'].push(RoutingPass.HOME);
  }

  private handlerClickCreateAccount(): void {
    this.setState({
      isDisplayCreateAccount: true,
    });
  }

  private handlerClickRetakePass(): void {
    this.setState({
      isDisplayRetakePass: true,
    })
  }

}



