
export class DummyImgGetter {

  // 使用サイト
  // https://placehold.jp/#advanced-tab

  static getURL(patternId: DummyImgPattern = DummyImgPattern.gray, w: number = 150, h: number = 150): string {
    const host: string = 'http://placehold.jp';
    const color_bg: string = DummyImgGetter.getColorStr(patternId);
    const color_msg: string = 'bbbbbb';
    return `${host}/${color_bg}/${color_msg}/${w}x${h}.png`;
  }

  private static getColorStr(patternId: DummyImgPattern): string {
    switch (patternId) {
      case DummyImgPattern.gray:
        return 'd3d3d3';
      case DummyImgPattern.blue:
        return 'add8e6';
      case DummyImgPattern.pink:
        return 'ffb6c1';
      case DummyImgPattern.green:
        return '90ee90';
      case DummyImgPattern.white:
        return 'ffffff';
    }
  }

}

enum DummyImgPattern {
  gray,
  blue,
  pink,
  green,
  white,
}



