import * as React from 'react';
import headerToBack from '../../../../../assets/image/header/header_to_back.svg';
import {NavigationBarComponent} from '../../../../common/navigation-bar-component/navigation-bar-component';
import {RoutingPass} from '../../../../../constant/routing-pass';

interface CreateAccountComponentProps {
  callbackIsDisplay: () => void;
}

interface CreateAccountComponentState {
  inputEmail: string,
  inputPass: string,
  inputPassConfirm: string,
  inputNickname: string,
}

export class CreateAccountComponent extends React.Component<CreateAccountComponentProps, CreateAccountComponentState> {

  constructor(props: CreateAccountComponentProps) {
    super(props);
    this.state = {
      inputEmail: '',
      inputPass: '',
      inputPassConfirm: '',
      inputNickname: '',
    };
  }

  render() {
    return (
      <div className={'body_area pass_body'}>

        <div className={'header_wrap config_header'}>
          <NavigationBarComponent
            callback={() => this.props.callbackIsDisplay()}
            pageTitle={'新規登録'}
          />
        </div>

        <div className={'listStyle_wrap passChangeList'}>
          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              メールアドレス
            </div>
            <div className={'listItem_right'}>
              <input
                onChange={(e) => this.setState({inputEmail: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              パスワード
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPass: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left position_r'}>
              パスワード<div className={'abs_text'}>(確認)</div>
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPassConfirm: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              ニックネーム
            </div>
            <div className={'listItem_right'}>
              <input
                onChange={(e) => this.setState({inputNickname: e.target.value})}
              />
            </div>
          </div>

        </div>

        <div className={'divide30'} />

        <div className={'btn_wrap'}>
          <button
            className={'reset btnStyle_1'}
            onClick={() => this.handlerClickCreateAccount()}
          >
            登録する
          </button>
        </div>

        <div className={'divide20'} />

      </div>

    );
  }

  private handlerClickCreateAccount(): void {
    const email = this.state.inputEmail;
    const pass = this.state.inputPass;
    const passConfirm = this.state.inputPassConfirm;
    const nickname = this.state.inputNickname;
    console.log(email, pass,passConfirm, nickname);
    this.props.callbackIsDisplay();
  }

}


