import * as React from 'react';
import {NavigationBarComponent} from '../../../../common/navigation-bar-component/navigation-bar-component';

interface SendMailComponentProps {
  callbackIsDisplay: () => void;
}

interface SendMailComponentState {
  inputEmail: string;
}

export class SendMailComponent extends React.Component<SendMailComponentProps, SendMailComponentState> {

  constructor(props: SendMailComponentProps) {
    super(props);
    this.state = {
      inputEmail: '',
    };
  }

  render() {
    return (
      <div className={'body_area pass_body'}>

        <div className={'header_wrap config_header'}>
          <NavigationBarComponent
            callback={() => this.props.callbackIsDisplay()}
            pageTitle={'パスワード変更'}
          />
        </div>

        <br /><br />
        入力したメールアドレスに記載されているパスワード再設定用アドレスからパスワードを再設定してください。
        <br /><br /><br />


        <div className={'listStyle_wrap passChangeList'}>
          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              メールアドレス
            </div>
            <div className={'listItem_right'}>
              <input
                onChange={(e) => this.setState({inputEmail: e.target.value})}
              />
            </div>
          </div>

        </div>

        <div className={'divide30'} />

        <div className={'btn_wrap'}>
          <button
            className={'reset btnStyle_1'}
            onClick={() => this.handlerClickSendMail()}
          >
            送信する
          </button>
        </div>

        <div className={'divide20'} />

      </div>
    );
  }

  private handlerClickSendMail(): void {
    this.props.callbackIsDisplay();
  }

}


