import {CorrespondStatus} from '../enum/correspond-status';

export class CorrespondStatusCheckServie {
  private static _ins: CorrespondStatusCheckServie;

  private constructor() {
  }

  static get ins(): CorrespondStatusCheckServie {
    if (!this._ins) {
      this._ins = new CorrespondStatusCheckServie();
    }
    return this._ins;
  }

  public returnCorrespondStatusAsString(correspondStatus: CorrespondStatus): string {
    switch (correspondStatus) {
      case CorrespondStatus.NotYet:
        return '未対応';
      case CorrespondStatus.During:
        return '対応中';
      case CorrespondStatus.Complete:
        return '完了';
      case CorrespondStatus.Accepted:
        return '受付済み';
      default:
        return '';
    }
  }
  public returnCorrespondStatusAsEnum(correspondString: String): CorrespondStatus {
    switch (correspondString) {
      case '未対応':
        return CorrespondStatus.NotYet;
      case '対応中':
        return CorrespondStatus.During;
      case '完了':
        return CorrespondStatus.Complete;
      case '受付済み':
        return CorrespondStatus.Accepted;
      default:
        return CorrespondStatus.NotYet;
    }
  }

}



