import * as React from 'react';
import './config-profile-component.scss'
{/* import iconArrowDown from '../../../../assets/image/common/icon_arrow_down.svg' */}

interface ConfigProfileComponentProps {
}

interface ConfigProfileComponentState {
  user_id: string;
  user_name: string;
  user_age: number | null;
}

export class ConfigProfileComponent extends React.Component<ConfigProfileComponentProps, ConfigProfileComponentState> {

  constructor(props: ConfigProfileComponentProps) {
    super(props);
    this.state = {
      user_id: '',
      user_name: '',
      user_age: null,
    }
  }

  render() {
    const user_id = this.state.user_id;
    const user_name = this.state.user_name;
    const user_age = this.state.user_age;
    return (
      <div className={'body_area pass_body'}>

        <div className={'listStyle_wrap profileEdit'}>
          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
                ID
            </div>
            <div className={'listItem_right innerText'}>
              {user_id}
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              ユーザー名
            </div>
            <div className={'listItem_right'}>
              <input
                value={user_name}
                onChange={(e) => this.setState({user_name: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              年齢
            </div>
            <div className={'listItem_right editAge flex_box flex_align_center'}>
              <input
                type={'number'}
                value={user_age || ''}
                onChange={(e) => this.setState({user_age: Number(e.target.value) || null})}
              />歳
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              性別
            </div>
            <div className={'listItem_right'}>
              <select>
                <option>男性</option>
                <option>女性</option>
              </select>
            </div>
          </div>
        </div>

        <div className={'divide30'} />

        <div className={'btn_wrap'}>
          <button className={'reset btnStyle_1'}>変更する</button>
        </div>

      </div>
    );
  }

  componentWillMount(): void {
    this.getUserInfo();
  }

  private getUserInfo(): void {
    this.setState({
      user_id: '123456',
      user_name: 'hoge piyo',
      user_age: null,
    });
  }

}



