import * as React from 'react';
import {ItemListComponent} from './item-list-component/item-list-component';
import {Item} from './item';

interface AutoCompleteInputComponentProps {
  getList: (searchQuery: string) => Item[];
  onChange: (inputValue: Item) => void;
}

interface AutoCompleteInputComponentState {
  value: string;
  isInput: boolean;
}

export class AutoCompleteInputComponent extends React.Component<AutoCompleteInputComponentProps, AutoCompleteInputComponentState> {

  constructor(props: AutoCompleteInputComponentProps) {
    super(props);
    this.state = {
      value: '',
      isInput: false,
    };
  }

  render() {
    const value = this.state.value;
    const isInput = this.state.isInput;
    const getList = this.props.getList;
    return (
      <div>

        <div>
          <input
            value={value}
            onChange={(e) => this.handlerChangeInput(e.target.value)}
          />
        </div>

        {isInput ? (
          <div className={'suggest_wrap'}>
            <ItemListComponent
              callbackSelectItem={(item) => this.handlerSelectItem(item)}
              itemList={getList(value)}
            />
          </div>
        ) : (<></>)}

      </div>
    );
  }

  private handlerChangeInput(inputValue: string): void {
    this.setState({
      value: inputValue,
      isInput: !!inputValue,
    });
  }

  private handlerSelectItem(item: Item): void {
    const onChange = this.props.onChange;
    this.setState({
      value: item.label,
      isInput: false,
    });
    onChange(item);
  }

}



