import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import {QueryParamFormatter} from '../../../../util/query-param-formatter';

interface ForgetComponentProps {
  inputPass: string,
  inputPassConfirm: string,
}

interface ForgetComponentState {
  inputPass: string,
  inputPassConfirm: string,
}

export class ForgetComponent extends React.Component<ForgetComponentProps, ForgetComponentState> {
  private queryParam: string = '';

  constructor(props: ForgetComponentProps) {
    super(props);
    this.state = {
      inputPass: '',
      inputPassConfirm: '',
    };
    this.parse();
    console.log(this.queryParam);
  }

  render() {
    return (
      <div className={'body_area pass_body'}>

        <div className={'header_wrap config_header'}>
          <div className={'headerTitle'}>
            パスワード再設定
          </div>
        </div>

        <div className={'listStyle_wrap passChangeList'}>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left'}>
              パスワード
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPass: e.target.value})}
              />
            </div>
          </div>

          <div className={'listItem_wrap'}>
            <div className={'listItem_left position_r'}>
              パスワード
              <div className={'abs_text'}>(確認)</div>
            </div>
            <div className={'listItem_right'}>
              <input
                type={'password'}
                onChange={(e) => this.setState({inputPassConfirm: e.target.value})}
              />
            </div>
          </div>

        </div>

        <div className={'divide30'}/>

        <div className={'btn_wrap'}>
          <button
            className={'reset btnStyle_1'}
            onClick={() => this.handlerClickGotoLogin()}
          >
            設定する
          </button>
        </div>

        <div className={'divide20'}/>

      </div>
    );
  }

  private handlerClickGotoLogin(): void {
    this.props['history'].push(RoutingPass.LOGIN);
  }

  private parse(): void {
    const parsed = QueryParamFormatter.queryParse(location.href);
    this.queryParam = parsed.url;
  }

}


