import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import {Contribution} from '../../../../model/contribution';
import minEkiKun from 'src/assets/min-eki-kun.png';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import {QueryParamFormatter} from '../../../../util/query-param-formatter';
import './page-contribution-detail-component.scss';
import iconArrowRight from '../../../../assets/image/common/icon_arrow_right.svg';
import {CorrespondStatus} from '../../../../enum/correspond-status';
import {CorrespondStatusCheckServie} from '../../../../service/correspond-status-check-servie';
import {DummyImgGetter} from '../../../../util/dummy-img-getter';

interface PageWebContributionDetailComponentProps {
  history: any;
  isAll: boolean;
}

interface PageWebContributionDetailComponentState {
  isLoading: boolean;
  isEdit: boolean;
  contributionDetail: Contribution | null;
}

export class PageContributionDetailComponent extends React.Component<PageWebContributionDetailComponentProps, PageWebContributionDetailComponentState> {
  private contributionDetail: Contribution | null;
  private query: string = '';
  private route: string = '';
  private station: string = '';
  private status: string = '';

  constructor(props: PageWebContributionDetailComponentProps) {
    super(props);
    this.state = {
      isLoading: true,
      isEdit: false,
      contributionDetail: null,
    };
    this.parse();
  }

  componentWillMount(): void {
    this.setState({
      isLoading: false,
      contributionDetail: new Contribution(
        0,
        '駅大好きマニア',
        '尾崎',
        '2番線ホーム',
        '電球',
        'ちかちかしている',
        DummyImgGetter.getURL(0, 3024,4032),
        '',
        CorrespondStatus.During,
        new Date(),
      ),
    });
  }

  render() {
    const isAll = this.props.isAll;
    const isLoading = this.state.isLoading;
    const isEdit = this.state.isEdit;
    const contributionDetail = this.state.contributionDetail;
    return (
      <div>

        <div
          className={isAll ? 'header_wrap postListAll' : 'header_wrap pastPostList'}
        >
          <NavigationBarComponent
            callback={() => this.props['history'].push(isAll ? RoutingPass.ALL_CONTRIBUTION_LIST + '?' + this.query : RoutingPass.MY_CONTRIBUTION)}
            pageTitle={isEdit ? '編集画面' : '詳細画面'}
          />
        </div>

        <div className={'body_area postDetail_body'}>
          {isLoading || !contributionDetail ? (
            <div className={'loading_wrap'}>
              <div className={'loader'}>ロード中</div>
            </div>
          ) : (
            <div
              className={isEdit ? 'detail_wrap modeEdit' : 'detail_wrap'}
            >

              {isAll ? (
                <div className={'detailItem_wrap flex_box'}>
                  <div className={'innerLeft'}>投稿者</div>
                  <div className={'innerRight'}>{contributionDetail.contributor}</div>
                </div>
              ) : (
                <></>
              )}

              <div className={'detailItem_wrap flex_box'}>
                <div className={'innerLeft'}>駅</div>

                {isEdit ? (
                  <div className={'innerRight flex_box flex_align_center'}>
                    <div>
                      <input
                        value={contributionDetail.station}
                        onChange={(e) => this.editContribution(1, e.target.value)}
                      />
                    </div>
                    <div>
                      <img src={iconArrowRight}/>
                    </div>
                  </div>
                ) : (
                  <div className={'innerRight'}>
                    {contributionDetail.station}
                  </div>
                )}
              </div>

              <div className={'detailItem_wrap flex_box'}>
                <div className={'innerLeft'}>場所</div>

                {isEdit ? (
                  <div className={'innerRight flex_box flex_align_center'}>
                    <div>
                      <input
                        value={contributionDetail.site}
                        onChange={(e) => this.editContribution(2, e.target.value)}
                      />
                    </div>
                    <div>
                      <img src={iconArrowRight}/>
                    </div>
                  </div>
                ) : (
                  <div className={'innerRight'}>
                    {contributionDetail.site}
                  </div>
                )}
              </div>

              <div className={'detailItem_wrap flex_box'}>
                <div className={'innerLeft'}>設備</div>

                {isEdit ? (
                  <div className={'innerRight flex_box flex_align_center'}>
                    <div>
                      <input
                        value={contributionDetail.facility}
                        onChange={(e) => this.editContribution(3, e.target.value)}
                      />
                    </div>
                    <div>
                      <img src={iconArrowRight}/>
                    </div>
                  </div>
                ) : (
                  <div className={'innerRight'}>
                    {contributionDetail.facility}
                  </div>
                )}
              </div>

              <div className={'detailItem_wrap flex_box'}>
                <div className={'innerLeft'}>カテゴリ</div>

                {isEdit ? (
                  <div className={'innerRight flex_box flex_align_center'}>
                    <div>
                      <input
                        value={contributionDetail.category}
                        onChange={(e) => this.editContribution(4, e.target.value)}
                      />
                    </div>
                    <div>
                      <img src={iconArrowRight}/>
                    </div>
                  </div>
                ) : (
                  <div className={'innerRight'}>
                    {contributionDetail.category}
                  </div>
                )}
              </div>

              <div className={'detailItem_wrap position_r img_wrap flex_box'}>
                {isEdit ? (
                  <>
                    <img
                      src={contributionDetail.thumbnail}
                    />
                    <button className={'reset btn_delete'}>×</button>
                    <button
                      onClick={() => this.handlerClickReTakeImg()}
                      className={'reset btn_retake'}
                    >
                      撮り直しをする
                    </button>
                  </>
                ) : (
                  <img
                    src={contributionDetail.thumbnail}
                  />
                )}
              </div>

              <div className={'detailItem_wrap flex_box'}>
                <div className={'innerLeft'}>備考</div>

                {isEdit ? (
                  <div className={'innerRight flex_box flex_align_center'}>
                    <div>
                      <input
                        value={contributionDetail.remarks}
                        onChange={(e) => this.editContribution(6, e.target.value)}
                      />
                    </div>
                    <div>
                      <img src={iconArrowRight}/>
                    </div>
                  </div>
                ) : (
                  <div className={'innerRight'}>
                    {contributionDetail.remarks}
                  </div>
                )}
              </div>

              <div className={'detailItem_wrap status_wrap flex_box'}>
                <div className={'innerLeft'}>ステータス</div>
                <div className={'innerRight'}>
                  {/* TODO 投稿一覧と同様のクラスを付与してください */}
                  {/*<span className={'unsupported'}>{contributionDetail.status}</span>*/}
                  {contributionDetail.status === CorrespondStatus.NotYet ? (
                    <span className={'unsupported'}>
                      {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(contributionDetail.status)}
                    </span>
                  ) : contributionDetail.status === CorrespondStatus.During ? (
                    <span className={'responding'}>
                      {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(contributionDetail.status)}
                    </span>
                  ) : (
                    <span className={'complete'}>
                      {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(contributionDetail.status)}
                    </span>
                  )}
                </div>
              </div>

              <div className={'divide30'}/>

              <div className={'btn_wrap flex_box'}>
                <div>
                  <button
                    className={'reset btnStyle_1'}
                    onClick={() => isEdit ? this.setState({
                      isEdit: false,
                      contributionDetail: this.contributionDetail,
                    }) : this.handlerClickGotoList()}
                  >
                    {isEdit ? (
                      <>
                        キャンセル
                      </>
                    ) : (
                      <>
                        一覧に戻る
                      </>
                    )}
                  </button>
                </div>
                {isAll ? (<></>) : (
                  <div>
                    <button
                      className={'reset btnStyle_1'}
                      onClick={() => isEdit ? this.handlerClickChangeContributionInfo() : this.handlerClickEdit()}
                    >
                      {isEdit ? (
                        <>
                          変更する
                        </>
                      ) : (
                        <>
                          編集する
                        </>
                      )}
                    </button>
                  </div>
                )}
              </div>

            </div>
          )}
        </div>

      </div>
    );
  }

  componentDidMount(): void {
    window.scrollTo(0, 0);
  }

  private parse(): void {
    const parsed = QueryParamFormatter.queryParse(location.href);
    this.route = parsed.route;
    this.station = parsed.station;
    this.status = parsed.status;
    this.query = QueryParamFormatter.queryCompose({route: this.route, station: this.station, status: this.status});
  }

  private handlerClickGotoList(): void {
    this.props['history'].push(this.props.isAll ? RoutingPass.ALL_CONTRIBUTION_LIST + '?' + this.query : RoutingPass.MY_CONTRIBUTION);
  }

  private handlerClickEdit(): void {
    this.contributionDetail = this.state.contributionDetail;
    this.setState({
      isEdit: true,
    });
  }

  private handlerClickChangeContributionInfo(): void {
    this.setState({
      isEdit: false,
    });
  }

  private editContribution(
    num: number,
    value: string,
  ): void {
    if (!this.state.contributionDetail) {
      return;
    }
    const id = this.state.contributionDetail.id;
    const contributor = this.state.contributionDetail.contributor;
    const station = num === 1 ? value : this.state.contributionDetail.station;
    const site = num === 2 ? value : this.state.contributionDetail.site;
    const facility = num === 3 ? value : this.state.contributionDetail.facility;
    const category = num === 4 ? value : this.state.contributionDetail.category;
    const thumbnail = num === 5 ? value : this.state.contributionDetail.thumbnail;
    const remarks = num === 6 ? value : this.state.contributionDetail.remarks;
    const status = this.state.contributionDetail.status;
    const date = new Date();
    this.setState({
      contributionDetail: new Contribution(
        id,
        contributor,
        station,
        site,
        facility,
        category,
        thumbnail,
        remarks,
        status,
        date,
      ),
    });
  }

  private handlerClickReTakeImg(): void {
    const inputElement: HTMLInputElement = document.createElement('input');
    inputElement.type = 'file';
    inputElement.accept = 'image/png, image/jpeg';
    inputElement.className = 'hideEle';
    inputElement.onchange = (e: any) => {
      this.readImgFile((e.target).files[0]);
      document.body.removeChild(inputElement);
    };
    inputElement.value = '';
    document.body.appendChild(inputElement);
    inputElement.click();
  }

  private readImgFile(file: File): void {
    const reader: FileReader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
      if (reader.error) {
        console.error(reader.error);
        return;
      }
      if (this.state.contributionDetail) {
        const id = this.state.contributionDetail.id;
        const contributor = this.state.contributionDetail.contributor;
        const station = this.state.contributionDetail.station;
        const site = this.state.contributionDetail.site;
        const facility = this.state.contributionDetail.facility;
        const category = this.state.contributionDetail.category;
        const thumbnail = reader.result as string;
        const remarks = this.state.contributionDetail.remarks;
        const status = this.state.contributionDetail.status;
        const date = new Date();
        this.state.contributionDetail.thumbnail = thumbnail;
        this.setState({
          contributionDetail: this.state.contributionDetail,
        });
      }
    };
  }

}



