import * as React from 'react';
import {ConfigMenuComponent} from './menu/config-menu-conponent';
import {ConfigPassComponent} from './pass/config-pass-component';
import {ConfigProfileComponent} from './profile/config-profile-component';
import {DebugService} from '../../../service/debug-service';
import './config-conponent.scss';
import headerToConfig from '../../../assets/image/header/header_to_config.svg';
import headerToBack from '../../../assets/image/header/header_to_back.svg';


interface ConfigComponentProps {
  callbackDisplayHome: (isDisplay: boolean) => void;
}

interface ConfigComponentState {
  displayMode: ConfigType;
  isDisplay: boolean;
}

export class ConfigComponent extends React.Component<ConfigComponentProps, ConfigComponentState> {

  constructor(props: ConfigComponentProps) {
    super(props);
    this.state = {
      displayMode : ConfigType.MENU,
      isDisplay: false,
    };
  }

  render() {
    const displayMode = this.state.displayMode;
    return (
      <>

        {this.state.isDisplay ? (
          <div className={'config_wrap'}>

            <div className={'header_wrap config_header'}>
              <button
                className={'reset inHeaderBtn inHeaderLeftBtn'}
                onClick={() => this.changeIsDisplay(false)}
              >
                <img src={headerToBack} />
              </button>

              <div className={'headerTitle'}>
                {displayMode === ConfigType.MENU ? (
                  <>
                    設定
                  </>
                ) : displayMode === ConfigType.PASS ? (
                  <>
                    パスワード変更
                  </>
                ) : (
                  <>
                    プロフィール変更
                  </>
                )}
              </div>
            </div>

        {displayMode === ConfigType.MENU ? (
          <ConfigMenuComponent
            callbackPass={() => this.changeDisplayMode(ConfigType.PASS)}
            callbackProfile={() => this.changeDisplayMode(ConfigType.PROFILE)}
          />
        ) : displayMode === ConfigType.PASS ? (
          <ConfigPassComponent/>
        ) : displayMode === ConfigType.PROFILE ? (
          <ConfigProfileComponent/>
        ) : (<div />)}

          </div>
        ) : (
          <div className={'header_wrap home_header'}>
            <button
              className={'reset inHeaderBtn inHeaderRightBtn'}
              onClick={() => this.changeIsDisplay(true)}
            >
              <img src={headerToConfig} />
            </button>

            <div className={'headerTitle'}>
              ホーム
            </div>
          </div>
        )}

      </>
    );
  }

  private changeIsDisplay(isDisplay: boolean): void {
    if(isDisplay){
      this.setState({
        isDisplay: isDisplay
      });
      this.props.callbackDisplayHome(!isDisplay);
    }else if(this.state.displayMode === ConfigType.MENU){
      this.setState({
        displayMode: isDisplay ? this.state.displayMode : ConfigType.MENU,
        isDisplay: isDisplay,
      });
      this.props.callbackDisplayHome(!isDisplay);
    }else{
      this.setState({
        displayMode: ConfigType.MENU
      });
    }
  }

  private changeDisplayMode(mode: ConfigType) : void {
    this.setState({
      displayMode: mode
    });
  }

}

export enum ConfigType {
  MENU,
  PASS,
  PROFILE,
  DEFAULT,
}



