import * as React from 'react';
import {QuestionAndAnswer, QuestionTitle} from '../page-contribution-post-component';
import minEkiKun from 'src/assets/min-eki-kun.png';
import './contribution-confirm-component.scss';

interface ConfirmContributionInfoComponentProps {
  answerList: QuestionAndAnswer[];
  callbackOK: () => void;
  callbackAgain: () => void;
}

interface ConfirmContributionInfoComponentState {
  isComplete: boolean;
}

export class ContributionConfirmComponent extends React.Component<ConfirmContributionInfoComponentProps, ConfirmContributionInfoComponentState> {

  constructor(props: ConfirmContributionInfoComponentProps) {
    super(props);
    this.state = {
      isComplete: false,
    };
  }

  componentWillMount(): void {
    this.setState({
      isComplete: false,
    });
  }

  render() {
    const answerList = this.props.answerList;
    const isComplete = this.state.isComplete;
    return (
      <div className={'talk_wrap confirm_wrap'}>
        <div className={'q_and_a_wrap'}>
          <div className={'question_wrap flex_box'}>
            <div className={'thumb_wrap'}>
              <img
                src={minEkiKun}
              />
            </div>

            <div className={'message_wrap'}>
              <div className={'messageInner_wrap'}>

                <div className={'confirmHead'}>
                  報告内容の確認をお願いします。
                </div>

                <div className={'divide10'}/>

                {answerList.map((answer, i) => (
                  <div key={i} className={'confirm_answer_item'}>
                    {answer.questionTitle === QuestionTitle.thumbnail ? (
                      <div className={'img_wrap'}>
                        <img
                          src={answer.value || ''}
                        />
                      </div>
                    ) : (
                      <div className={'flex_box'}>
                        <div>{answer.questionTitleValue}</div>
                        <div>{answer.value}</div>
                      </div>
                    )}
                  </div>
                ))}

                <div className={'bottom_btn_wrap'}>
                  {/*
                    TODO 完了に移行したら、二つのボタン両方に"disabled"のクラスを追加してください
                  */}
                  <button
                    className={'reset doubleBtnStyle' + (isComplete ? ' disabled' : '')}
                    onClick={() => this.props.callbackAgain()}
                  >
                    やりなおし
                  </button>
                  <button
                    className={'reset doubleBtnStyle' + (isComplete ? ' disabled' : '')}
                    onClick={() => {
                      this.setState({
                        isComplete: true,
                      });
                      this.props.callbackOK();
                    }}
                  >
                    この内容で報告
                  </button>
                </div>

              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }

}



