export class RoutingPass {

  private static _HOME: string = '/home';
  static get HOME(): string {
    return this._HOME;
  }

  private static _ADMIN: string = '/admin';
  static get ADMIN(): string {
    return this._ADMIN;
  }

  private static _LOGIN: string = '/login';
  static get LOGIN(): string {
    return this._LOGIN;
  }

  private static _FORGET: string = '/forget';
  static get FORGET(): string {
    return this._FORGET;
  }

  private static _ABOUT: string = '/about';
  static get ABOUT(): string {
    return this._ABOUT;
  }

  private static _REFINE_ALL_CONTRIBUTION : string = '/all-contribution';
  static get REFINE_ALL_CONTRIBUTION(): string {
    return this._REFINE_ALL_CONTRIBUTION;
  }
  private static _ALL_CONTRIBUTION_LIST: string = '/all-contribution/list';
  static get ALL_CONTRIBUTION_LIST(): string {
    return this._ALL_CONTRIBUTION_LIST;
  }
  private static _ALL_CONTRIBUTION_DETAIL: string = '/all-contribution/list/detail';
  static get ALL_CONTRIBUTION_DETAIL(): string {
    return this._ALL_CONTRIBUTION_DETAIL;
  }

  private static _POST_CONTRIBUTION: string = '/contribution/post';
  static get POST_CONTRIBUTION(): string {
    return this._POST_CONTRIBUTION;
  }

  private static _POST_CONTRIBUTION_FROM_LIST: string = '/my-contribution/post';
  static get POST_CONTRIBUTION_FROM_LIST(): string {
    return this._POST_CONTRIBUTION_FROM_LIST;
  }

  private static _MY_CONTRIBUTION: string = '/my-contribution';
  static get MY_CONTRIBUTION(): string {
    return this._MY_CONTRIBUTION;
  }

  private static _CONTRIBUTION_DETAIL: string = '/my-contribution/detail';
  static get CONTRIBUTION_DETAIL(): string {
    return this._CONTRIBUTION_DETAIL;
  }

  private static _INFO: string = '/info';
  static get INFO(): string {
    return this._INFO;
  }

}



