import {Route} from '../model/route';

export class RouteList {
  private route: Route[]
  constructor(){
    this.route = this.stationList();
  }

  private stationList(): Route[] {
    const arr: Route[] = [
      new Route(1, '11415', 'JR北陸本線(米原～金沢)', '1'),
      new Route(2, '11416', 'JR高山本線', '2'),
      new Route(3, '11417', 'JR城端線', '3'),
      new Route(4, '11418', 'JR氷見線', '4'),
      new Route(5, '11419', 'JR七尾線', '5'),
      new Route(6, '11420', 'JR越美北線', '6'),
      new Route(7, '11421', 'JR小浜線', '7'),
      new Route(8, '11422', 'JR草津線', '8'),
      new Route(9, '11423', 'JR東海道本線(米原～京都)', '9'),
      new Route(10, '11424', 'JR湖西線', '10'),
    ];
    return arr;
  }

  public getList(inputValue: string): Route[] {
    const arr: Route[] = [];
    this.route.map((data) => {
      if(data.name.indexOf(inputValue) === 0){
        arr.push(data);
      }
    });
    return arr;
  }
}


