import * as React from 'react';
import './config-menu-component.scss'
import iconArrowRight from '../../../../assets/image/common/icon_arrow_right.svg'

interface ConfigMenuComponentProps {
  callbackPass: () => void;
  callbackProfile: () => void;
}

interface ConfigMenuComponentState {
}

export class ConfigMenuComponent extends React.Component<ConfigMenuComponentProps, ConfigMenuComponentState> {

  constructor(props: ConfigMenuComponentProps) {
    super(props);
  }

  render() {
    const callbackPass = this.props.callbackPass;
    const callbackProfile = this.props.callbackProfile;
    return (
      <div className={'body_area config_body'}>
        <div className={'bodyInner_wrap'}>
          <div className={'listStyle_wrap'}>

            <button onClick={() => callbackPass()} className={'listItem_wrap reset flex_box flex_align_center'}>
              <span className={'listItem_left'}>
                パスワード変更
              </span>
              <span className={'listItem_right'}>
                <img src={iconArrowRight} />
              </span>
            </button>

            <button onClick={() => callbackProfile()} className={'listItem_wrap reset flex_box flex_align_center'}>
              <span className={'listItem_left'}>
                プロフィール変更
              </span>
              <span className={'listItem_right'}>
                <img src={iconArrowRight} />
              </span>
            </button>

            <button onClick={() => this.handlerClickButtonLogout()} className={'listItem_wrap reset flex_box flex_align_center'}>
              <span className={'listItem_left'}>
                ログアウト
              </span>
              <span className={'listItem_right'}>
                <img src={iconArrowRight} />
              </span>
            </button>

          </div>
        </div>
      </div>
    );
  }

  private handlerClickButtonLogout(): void {
    console.log('hoge');
  }

}



