import {CorrespondStatus} from '../enum/correspond-status';

export class Contribution {
  constructor(
    /* id */
    public id: number,
    /* 投稿者 */
    public contributor: string,
    /* 駅 */
    public station: string,
    /* 発生箇所 */
    public site: string,
    /* 設備 */
    public facility: string,
    /* カテゴリ */
    public category: string,
    /* 画像 */
    public thumbnail: string,
    /* 備考 */
    public remarks: string,
    /* ステータス */
    public status: CorrespondStatus,
    /* 投稿日時 */
    public date: Date
  ){}
}



